body {
  margin: 0;
}
/* react-slick */
.slick-prev:before,
.slick-next:before {
  color: black !important;
}
/* alphaTab */
.at-cursor-bar {
  background: rgba(255, 242, 0, 0.25);
}
.a-selection div {
  background: rgba(64, 64, 255, 0.1);
}
.at-cursor-beat {
  background: rgba(64, 64, 255, 0.75);
  width: 3px;
}
.at-highlight * {
  fill: var(--blue);
  stroke: var(--blue);
}
